import "./App.css";
import { useState, useRef, useEffect } from "react";
import image from "./assets/chart.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faUpload,
  faTimesCircle,
  faEye,
  faEyeSlash,
} from "@fortawesome/free-solid-svg-icons";
import Modal from "react-modal";
import axios from "axios";

function App() {


  const [teamMembers, setTeamMembers] = useState([""]);
  const [spinShow, setSpinShow] = useState(false);
  const [selectedFile, setSelectedFile] = useState();
  const [isSelected, setIsSelected] = useState(false);
  const [alert, setAlert] = useState(false);
  const [message, setMessage] = useState("");
  const [titre, settitre] = useState("");
  const [color, setcolor] = useState("");
  const [colorModal, setColorModal] = useState("");
  const [teamName, setTeamName] = useState("");
  const [password, setPassword] = useState("");
  const [response, setResponse] = useState("");
  const [responseColor, setResponseColor] = useState("green");
  const [responseMemberTwo, setResponseMemberTwo] = useState("");
  const [responseMemberOne, setResponseMemberOne] = useState("");
  const [disableButtonTeam, setDisableButtonTeam] = useState(true);
  const [disableButtonLeader, setDisableButtonLeader] = useState(true);
  const [MultipleOne, setMultipleOne] = useState(0)
  const [MultipleTwo, setMultipleTwo] = useState(0)

  const [memberOne, setMemberOne] = useState("");
  const [memberTwo, setMemberTwo] = useState("");

  const [stepsList, setStepsList] = useState([false, true, true]);


  const [PointerStyle, setPointerStyle] = useState("")


  const Next = (position) => {

    let list = [...stepsList];
    list[position] = true
    list[position + 1] = false;
    setStepsList(list);


  }

  const previous = (position) => {

    let list = [...stepsList];
    list[position] = true
    list[position - 1] = false;
    setStepsList(list);

  }


  const [NextButton, setNextButton] = useState(false)

  const [leadermsg, setleadermsg] = useState("");
  const [leaderUserName, setleaderUserName] = useState("");
  const [leadermsgcolor, setleadermsgcolor] = useState("");

  const registerLeader = (e) => {

    setleaderUserName(e.target.value);
    let formData = new FormData;
    formData.append("leader_username", e.target.value);


    axios
      .post("https://ensitgeeksclub.com/leader.php", formData)
      .then((res) => {
        setSpinShow(false);
        if (res.status == 200) {
          
          setDisableButtonLeader(false);
          setleadermsg(res.data);
          setleadermsgcolor("green");
        }
        if (res.status >= 201) {
          setleadermsg(res.data);
          setDisableButtonLeader(true);
          setleadermsgcolor("red");
        }
      })
      .catch((e) => {
        setColorModal("#f94416");
        setAlert(true);
        setMessage("Server error");
        setcolor("white");
        settitre("Error");
      });
  }
  const [memberOnemescolor, setMemberOnemescolor] = useState("");
  const [memberTwomescolor, setMemberTwomescolor] = useState("");

  const registerMemberOne = (e) => {
    
    if (e.target.value!="") {
      setMemberOne(e.target.value);
      if(e.target.value == memberTwo){
        setResponseMemberOne("Don't use the same username twice");
        setMemberOnemescolor("red");
        setNextButton(true);
        setMultipleOne(0)
        
      }else
      if(e.target.value!= leaderUserName ){
      let formData = new FormData;
      formData.append("username", e.target.value);


      axios
        .post("https://ensitgeeksclub.com/member.php", formData)
        .then((res) => {
          setSpinShow(false);
          if (res.status == 200) {
            
            setDisableButtonLeader(false);
            setResponseMemberOne(res.data);
            setMemberOnemescolor("green");
            setNextButton(false)
            setMultipleOne(20)
          }
          if (res.status >= 201) {

            setResponseMemberOne(res.data);
            setDisableButtonLeader(true);
            setMemberOnemescolor("red");
            setNextButton(true)
            setMultipleOne(0)
          }
        })
        .catch((e) => {
          setColorModal("#f94416");
          setAlert(true);
          setMessage("Server error");
          setcolor("white");
          settitre("Error");
          setNextButton(true)
          setMultipleOne(0)
        });
    }
    else{
      setResponseMemberTwo("Not allowed to use leader username");
      setNextButton(true)
      setMultipleOne(0)
    }
  }
     else {
      setMemberOne("");
      setResponseMemberOne("");
      setNextButton(false)
      setMultipleOne(0)
    }
  }

  const registerMemberTwo = (e) => {
    if (e.target.value!="") {
      if(e.target.value == memberOne){
        setResponseMemberTwo("Don't use the same username twice");
        setMemberTwomescolor("red");
        setNextButton(true);
        setMultipleTwo(0)
      }else
      if (e.target.value != leaderUserName) {
        setMemberTwo(e.target.value);
        let formData = new FormData;
        formData.append("username", e.target.value);


        axios
          .post("https://ensitgeeksclub.com/member.php", formData)
          .then((res) => {
            setSpinShow(false);
            if (res.status == 200) {
              
              setResponseMemberTwo(res.data);
              setMemberTwomescolor("green");
              setNextButton(false)
              setMultipleTwo(20)

            }
            if (res.status >= 201) {
              
              setResponseMemberTwo(res.data);
              setMemberTwomescolor("red");
              setNextButton(true);
              setMultipleTwo(0)

            }
          })
          .catch((e) => {
            setColorModal("#f94416");
            setAlert(true);
            setMessage("Server error");
            setcolor("white");
            settitre("Error");
            setNextButton(true)
            setMultipleTwo(0)

          });
      }
      else {
        setResponseMemberTwo("Not allowed to use leader username");
        setleadermsgcolor("red");
        setNextButton(true)
        setMultipleTwo(0)

      }

    } else {
      setMemberTwo("");
      setResponseMemberTwo("");
      setNextButton(false)
      setMultipleTwo(0)

    }
  }
  const registerTeamName = (e) => {
    if (!/[^a-zA-Z0-9_]/.test(e.target.value))

      if (5 <= e.target.value.length && e.target.value.length <= 15) {
        {
          setTeamName(e.target.value);
          let formData = new FormData;
          formData.append("team_tag", e.target.value);
          axios
            .post("https://ensitgeeksclub.com/teamname.php", formData)
            .then((res) => {
              setSpinShow(false);
              if (res.status == 200) {
                setDisableButtonTeam(false);
                setResponse(res.data);
                setResponseColor('green');
              }
              if (res.status >= 201) {
                
                setResponse(res.data);
                setDisableButtonTeam(true);
                setResponseColor('red');
              }
            })
            .catch((e) => {
              setColorModal("#f94416");
              setAlert(true);
              setMessage("Server error");
              setcolor("white");
              settitre("Error");
            });
        }
      }
      else { setResponse("Team tag length must contain at least 5 characters !!"); setResponseColor('red') }
    else { setResponse("Team tag must contain letters and numbers only !!"); setResponseColor('red'); }
  }



  const handleChange = (e, i) => {
    const value = e.target.value;
    const list = [...teamMembers];
    list[i] = value;
    setTeamMembers(list);
  };

  const changeHandler = (event) => {
    setSelectedFile(event.target.files[0]);
    setIsSelected(true);
  };

  function closeModal() {
    setAlert(false);
    setSpinShow(false);
  }
  const submit = (d) => {
    d.preventDefault();

    setSpinShow(true);

    if (
      teamName === "" ||
      leaderUserName === "" ||
      !selectedFile
    ) {
      setColorModal("#f94416");
      setAlert(true);
      setMessage("Please make sure to type all team informations !");
      setcolor("white");
      settitre("Error");
    } else {
      let formData = new FormData();

      formData.append("team_tag", teamName);
      formData.append("leader_username", leaderUserName);
      formData.append("member_one_username", memberOne);
      formData.append("member_two_username", memberTwo);
      formData.append("preuve", selectedFile);

      axios
        .post("https://ensitgeeksclub.com/paiement.php", formData)
        .then((res) => {
          setSpinShow(false);
          if (res.status == 200) {
            setColorModal("#41d888");
            setAlert(true);
            setMessage(res.data);
            setcolor("white");
            settitre("Success");
          }
          if (res.status >= 201) {
            setColorModal("#f94416");
            setAlert(true);
            setMessage(res.data);
            setcolor("white");
            settitre("Error");
          }
        })
        .catch((e) => {
          setColorModal("#f94416");
          setAlert(true);
          setMessage("Server error");
          setcolor("white");
          settitre("Error");
        });
    }
  };

  const [x, setx] = useState(0)
  const Ref = useRef(null);

 
  
    // The state for our timer
    const [timer, setTimer] = useState('00:00:00');
  
  
    const getTimeRemaining = (e) => {
        const total = Date.parse(e) - Date.parse(new Date());
        const seconds = Math.floor((total / 1000) % 60);
        const minutes = Math.floor((total / 1000 / 60) % 60);
        const hours = Math.floor((total / 1000 * 60 * 60) % 24);
        return {
            total, hours, minutes, seconds
        };
    }
  
  
    const startTimer = (e) => {
        let { total, hours, minutes, seconds } 
                    = getTimeRemaining(e);
        if (total >= 0) {
  
            // update the timer
            // check if less than 10 then we need to 
            // add '0' at the begining of the variable
            setTimer(
                (minutes > 9 ? minutes : '0' + minutes) + ':'
                + (seconds > 9 ? seconds : '0' + seconds)
            )
        }
    }
  
  
    const clearTimer = (e) => {
  
        // If you adjust it you should also need to
        // adjust the Endtime formula we are about
        // to code next    
        setTimer('30:00');
  
        // If you try to remove this line the 
        // updating of timer Variable will be
        // after 1000ms or 1sec
        if (Ref.current) clearInterval(Ref.current);
        const id = setInterval(() => {
            startTimer(e);
        }, 1000)
        Ref.current = id;
    }
  
    const getDeadTime = () => {
        let deadline = new Date();
  
        // This is where you need to adjust if 
        // you entend to add more time
        deadline.setSeconds(deadline.getSeconds() + 1800);
        return deadline;
    }
  
    // We can use useEffect so that when the component
    // mount the timer will start as soon as possible
  
    // We put empty array to act as componentDid
    // mount only
    useEffect(async () => {
        let a= await axios.get("https://ensitgeeksclub.com/number.php");
        setx(a.data);
        clearTimer(getDeadTime());
    }, []);

  // useEffect(() => {
  //   const timer = setTimeout(() => {
  //     if(stepsList!==[false,true,true]){setStepsList([false,true,true]);
  //     setColorModal("white");
  //         setAlert(true);
  //         setMessage("time up");
  //         setcolor("black");}
  //   }, 6000);
  //   return () => clearTimeout(timer);
  // }, []);
  if(timer == '00:00'){
    window.location.reload()
  }

  if (x == 1){
    return (
      <div className="screen">
        <div className="container"
         style={{
           margin:"7% auto",
         }}
        >
            <img src={image} />
            <br/>
            <br/>
            <h1 style={{
                  textAlign: "center",
                  color:'red'
                  // position:'relative',left:'35%'
                }}>Sorry the limit is reached !!!</h1>
        </div>
      </div>
    )
  }
  else
  return (
    <div className="screen">
      <div className="container">
        <img src={image} />
        <Modal
          isOpen={alert}
          onAfterOpen={() => {
            setAlert(true);
          }}
          onRequestClose={closeModal}
          style={{
            content: {
              top: "50%",
              left: "50%",
              right: "auto",
              bottom: "auto",
              marginRight: "-50%",
              transform: "translate(-50%, -50%)",
              // height: '25%',
              alignContent: "center",
              borderRadius: "4%",
              paddingTop: "0",
              paddingBottom: "2%",
              backgroundColor: colorModal,
              zIndex:100
            },
          }}
          ariaHideApp={false}
          contentLabel="Alert"
        >
          <h2 style={{ color: color }}>{titre}</h2>
          <div style={{ margin: "0 auto", fontSize: "16px", color: "white" }}>
            {message}
          </div>
        </Modal>
        <form
          onSubmit={submit}>
          <div className="row">
            <div
              style={{
                margin: "0 auto",
                width: "100%",
                // position:'relative',left:'35%'
              }}
            >
              <h2
                style={{
                  textAlign: "center",
                  // position:'relative',left:'35%'
                }}
              >
                Team Registration Form<br/>
              </h2>
                <h3 style={{
                  textAlign: "center",
                  color:'#1c83d1'
                  // position:'relative',left:'35%'
                }}>You have only {timer} to upload your request</h3>
            </div>

            <div hidden={stepsList[0]}>
              <input
                className="input-group.input-group-icon"
                type="text"
                placeholder="Team name"
                name="teamName"
                onChange={(e) => {
                  registerTeamName(e)
                }}
              />
              <p style={{ color: responseColor , paddingLeft:"1em" }}>{response}</p>

              <input
                className="input-group.input-group-icon"
                type="text"
                placeholder="Leader username"
                name="leader"
                onChange={(e) => {
                  registerLeader(e)
                }}
              />
              <p style={{ color: leadermsgcolor , paddingLeft:"1em"  }}>{leadermsg}</p>

              <input type="button" value="Next" className="btn" onClick={() => { Next(0) }} style={{ padding: "5px", height: "40px", marginTop: "1%" }} disabled={disableButtonLeader || disableButtonTeam} />

            </div>
            <div hidden={stepsList[1]}>
              <label
                className="doc"
                style={{
                  padding: "0",
                  margin: "1%",
                  marginTop: "0",
                  display: "flex",
                }}
              >
                <div style={{ width: "90%" }}>

                  <input
                    style={{ margin: "0", border: "none" }}
                    type="text"
                    name="Member_one"
                    placeholder="Member_one"
                    onChange={(e) => {
                      registerMemberOne(e);

                    }}
                  />

                </div>

              </label>
              <p style={{ color:memberOnemescolor  , paddingLeft:"1em" }}>{responseMemberOne}</p>

              <label
                className="doc"
                style={{
                  padding: "0",
                  margin: "1%",
                  marginTop: "0",
                  display: "flex",
                }}
              >
                <div style={{ width: "90%" }}>

                  <input
                    style={{ margin: "0", border: "none" }}
                    type="text"
                    name="Member_two"
                    placeholder="Member_two"
                    onChange={(e) => {
                      registerMemberTwo(e);

                    }}
                  />

                </div>

              </label>
              <p style={{ color:memberTwomescolor , paddingLeft:"1em"  }} >{responseMemberTwo}</p>

              <div className="btc">
                <p style={{ color:'#1c83d1'  , paddingLeft:"1em" }}><strong>You can skip this step if you are a solo candidate !</strong></p>
                <input type="button" value="Previous" className="btn" onClick={() => { previous(1) }} style={{ padding: "5px", height: "40px", marginTop: "1%" }} />

                <input type="button" value="Next" disabled={ NextButton } className="btn" onClick={() => { Next(1) }} style={{ padding: "5px", height: "40px", marginTop: "1%" }} />
              </div>
            </div>
            <div hidden={stepsList[2]}>
              <p style={{ paddingLeft: "2%" }}>
                <strong>
                  *Make sure to provide us with a photo of your payment receipt
                </strong>
              </p>
              <p style={{ paddingLeft: "2%",color:'#1c83d1'  }}>
                  *You have to pay <strong>{20+MultipleOne+MultipleTwo} DT</strong> by money transfer through <strong>D17</strong> app <strong>43308524</strong>

              </p>
              <label className="doc">
                <FontAwesomeIcon icon={faUpload} style={{ paddingRight: "1%" }} />
                {!selectedFile ? "Upload your document" : selectedFile.name}
                <input
                  className="input-group.input-group-icon"
                  hidden={true}
                  type="file"
                  placeholder="preuve"
                  name="preuve"
                  onChange={changeHandler}
                />
              </label>
              <br />
              {spinShow ? (
                <div style={{ margin: "0 auto" }} className="loader"></div>
              ) : (
                <>
                  <input type="button" value="Previous" className="btn" onClick={() => { previous(2) }} style={{ padding: "5px", height: "40px", marginTop: "1%" }} />
                  <input value="Submit" type="submit" className="btn"  style={{ padding: "5px", height: "40px", marginTop: "1%" }}/>
                </>
              )}
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default App;
